import React, { Component } from 'react'
import Link from 'gatsby-link'

class SearchPage extends Component {
    state = {
        query: '',
        results: []
    }

    componentDidMount(){
        let params = new URLSearchParams(document.location.search.substring(1));
        let q = params.get("q");
        
        this.getSearchResults(q);
    }

    render() {
        return (
            <div style={{
                padding: '0.5rem',
                maxWidth: '960px',
                margin: 'auto'
            }}>
                <h1>Results for {this.state.query}</h1>
                {this.state.results.length && this.state.results.map((result) => {
                    return (
                        <div key={result.id} style={{
                            marginBottom: '1rem'
                        }}>
                            <span style={{color: "gray"}}>{this.stripBaseUrl(`${result.link}`)}</span>
                            <br/>
                            <Link to={this.stripBaseUrl(`${result.link}`)}>
                                {result.title.rendered}
                            </Link>
                        </div>
                    )
                })}
            </div>
        )
    }

    getSearchResults = (q) => {
        let results = [];

        fetch(`https://curhestage.wpengine.com/wp-json/wp/v2/pages?search=${q}`)
        .then((res) => {
            return res.json()
        })
        .then((json) => {
            // console.log(json);
            
            this.setState({
                query: q,
                results: json
            })
        })
    }

    stripBaseUrl = (url) => {
        const base = "https://curhestage.wpengine.com/clinical-updates/"

        return url.replace(base, "")
    }
}

export default SearchPage



